.nextgen-modal {
  border-width: 0;
  box-shadow: none;
}
.nextgen-modal .components-modal__header {
  border-bottom: 0;
  height: 50px;
  margin: 0 -60px;
}
.nextgen-modal .components-modal__content {
  overflow-x: hidden;
  padding: 0 48px 30px;
  text-align: center;
}
.nextgen-modal__title {
  color: #1e1e1e;
  font-size: 1.15rem;
  margin-bottom: 1em;
  margin-top: 3px;
}
.nextgen-modal__subtitle {
  font-size: 1.1rem;
  font-weight: 300;
  margin-bottom: 1rem;
  margin-top: 2rem;
}
.nextgen-modal__warning {
  background-color: #f0f0f0;
  margin: 1.5em 0;
  position: relative;
}
.nextgen-modal__warning > p {
  padding: 1em 1em 1em 4em;
}
.nextgen-modal__warning > svg {
  fill: var(--active-orange-400);
  left: 1.5em;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.nextgen-modal p {
  font-size: 1.1em;
}
.nextgen-modal .components-button.is-primary {
  background-color: #1e1e1e;
  border-radius: 0;
  font-family: gdsherpa, Helvetica, Arial, sans-serif;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  font-weight: 600;
  height: 48px;
  line-height: 1;
  padding-left: 1.5em;
  padding-right: 1.5em;
  transition: background-color 0.2s;
}
.nextgen-modal .components-button.is-primary:disabled {
  color: #fff;
  opacity: 0.5;
  pointer-events: none;
}
.nextgen-modal .components-button.is-link {
  color: var(--wp-admin-theme-color);
  font-family: gdsherpa, Helvetica, Arial, sans-serif;
  font-size: 16px;
  padding: 1em;
  text-decoration: none;
}
.nextgen-modal .components-button.is-link:hover {
  color: #1e1e1e;
}

.components-modal__screen-overlay {
  z-index: 99999;
}

.themes-optout-modal {
  border-radius: 6px;
  font-size: 0.9rem;
  max-width: 700px;
}
.themes-optout-modal .components-modal__header {
  margin: 0 -4em;
  padding: 0 0.75em;
}
.themes-optout-modal .components-modal__header .components-button {
  background: none;
  border: 0;
}
.themes-optout-modal .components-modal__header .components-button:hover {
  cursor: pointer;
}
.themes-optout-modal .components-modal__header .components-button:hover > svg {
  fill: #135e96;
}
.themes-optout-modal .components-modal__header .components-button > svg {
  fill: #2271b1;
}
.themes-optout-modal .components-modal__header .components-button > span {
  display: none;
}
.themes-optout-modal .components-modal__content {
  padding: 0 4em;
  text-align: left;
}
.themes-optout-modal .nextgen-modal__title {
  text-align: center;
}
.themes-optout-modal .nextgen-modal__warning > svg {
  fill: #ef6c0f;
}
.themes-optout-modal__footer {
  background-color: #f0f0f0;
  display: flex;
  justify-content: flex-end;
  margin: 2em -4em 0;
  padding: 1.5em 4em;
}
.themes-optout-modal__footer__button {
  background-color: #2271b1;
  border: 1px solid #2271b1;
  border-radius: 4px;
  color: #fff;
  margin-left: 1em;
  padding: 0.75em 1.25em;
  transition: all 0.2s;
}
.themes-optout-modal__footer__button:hover {
  background-color: #135e96;
  border-color: #135e96;
  color: #fff;
  cursor: pointer;
}
.themes-optout-modal__footer__button--secondary {
  background-color: #fff;
  color: #2271b1;
}